:root {
  --font-body: "Poppins", sans-serif;
}

html {
  font-family: var(--font-body);
}

:root {
  --hue-color: 230;
  --first-color: hsl(var(--hue-color), 69%, 61%);
  --first-color-second: hsl(var(--hue-color), 69%, 61%);
  --first-color-alt: hsl(var(--hue-color), 57%, 53%);
  --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
  --title-color: hsl(var(--hue-color), 8%, 15%);
  --text-color: hsl(var(--hue-color), 8%, 45%);
  --text-color-light: hsl(var(--hue-color), 8%, 65%);
  --input-color: hsl(var(--hue-color), 70%, 96%);
  --body-color: hsl(var(--hue-color), 60%, 99%);
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 12%, 80%);
  --container-color: #fff;
}

body.dark-theme {
  --first-color-second: hsl(var(--hue-color), 30%, 8%);
  --first-color-lighter: hsl(var(--hue-color), 92%, 74%);
  --title-color: hsl(var(--hue-color), 8%, 95%);
  --text-color: hsl(var(--hue-color), 8%, 75%);
  --input-color: hsl(var(--hue-color), 29%, 16%);
  --body-color: hsl(var(--hue-color), 28%, 12%);
  --container-color: hsl(var(--hue-color), 29%, 16%);
  --scroll-bar-color: hsl(var(--hue-color), 12%, 48%);
  --scroll-thumb-color: hsl(var(--hue-color), 12%, 36%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: var(--normal-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

:root {
  --big-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;
  --smaller-font-size: 0.75rem;
  --font-medium: 500;
  --font-semi-bold: 600;
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-0-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}
@media (min-width: ) {
  :root {
    --big-font-size: 3rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: 0.875rem;
    --smaller-font-size: 0.813rem;
  }
}

:root {
  --header-height: 3rem;
}

.container {
  max-width: 61.25rem;
  margin-left: var(--mb-1-5);
  margin-right: var(--mb-1-5);
}
@media (max-width: 21.875em) {
  .container {
    margin: 0 auto;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}
@media (max-width: 60.5em) {
  .container {
    max-width: 50rem;
  }
}
@media (min-width: 48em) {
  .container {
    margin: 0 auto;
  }
}

.grid {
  display: grid;
  gap: 1.5rem;
}

section {
  padding: 2rem 0.5rem 4rem 0.5rem;
  margin: 0 auto;
}
@media (min-width: 48em) {
  section {
    padding: 6rem 0 2rem;
  }
}
section .section__title {
  font-size: var(--h1-font-size);
  text-align: center;
}
section .section__subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: var(--mb-3);
  text-align: center;
}

.home__container {
  gap: 1rem;
}
@media (min-width: 48em) {
  .home__container {
    row-gap: 5rem;
  }
}
.home__content {
  grid-template-columns: 0.5fr 3fr;
  padding-top: 3.5rem;
  align-items: center;
}
@media (max-width: 21.875em) {
  .home__content {
    grid-template-columns: 0.25fr 3fr;
  }
}
@media (min-width: 35.5em) {
  .home__content {
    grid-template-columns: max-content 1fr 1fr;
  }
}
@media (min-width: 48em) {
  .home__content {
    padding-top: 5.5rem;
    column-gap: 2rem;
    row-gap: 6rem;
  }
}
.home__social {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1rem;
}
@media (min-width: 64em) {
  .home__social {
    transform: translateX(-6rem);
  }
}
.home__social i {
  font-size: 1.25rem;
  color: var(--first-color);
}
.home__social i:hover {
  color: var(--first-color-alt);
}
@media (min-width: 35.5em) {
  .home__img {
    grid-row: 1;
    grid-column: 3;
    justify-self: center;
  }
}
.home__blob {
  width: 12.5rem;
  fill: var(--first-color);
}
@media (min-width: 48em) {
  .home__blob {
    width: 16.875rem;
  }
}
@media (min-width: 64em) {
  .home__blob {
    width: 20rem;
  }
}
.home__blobimg {
  width: 100%;
}
.home__text, .home__scroll {
  grid-column: 1/3;
}
.home__text .home__title, .home__scroll .home__title {
  font-size: var(--big-font-size);
}
.home__text .home__subtitle, .home__scroll .home__subtitle {
  font-size: var(--h3-font-size);
  color: var(--text-color);
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-0-75);
}
.home__text .home__description, .home__scroll .home__description {
  margin-bottom: var(--mb-2);
}
@media (min-width: 35.5em) {
  .home__text {
    grid-column: initial;
  }
}
.home .home__scroll {
  display: none;
}
@media (min-width: 48em) {
  .home .home__scroll {
    display: block;
  }
}
.home .home__scroll--link {
  color: var(--first-color);
  transition: 0.3s;
  display: inline-block;
}
.home .home__scroll--link:hover {
  transform: translateY(0.25rem);
  display: inline-block;
}
.home .home__scroll--link:hover .home__scroll--icon {
  transform: translateY(0.25rem);
  display: inline-block;
}
.home .home__scroll--name {
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  margin-right: var(--mb-0-25);
}
.home .home__scroll--icon {
  font-size: 1.25rem;
}
.home .button__icon {
  font-size: 1.25rem;
  margin-left: var(--mb-0-5);
  transition: 0.3s;
}

.btn {
  display: inline-block;
  background-color: var(--first-color);
  color: var(--container-color);
  padding: 1rem;
  border-radius: 0.5rem;
  font-weight: var(--font-medium);
}

.btn:hover {
  background-color: var(--first-color-alt);
}

.btn_flex {
  display: inline-flex;
  align-items: center;
}

.header {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--first-color-lighter);
}
@media (min-width: 48em) {
  .header {
    top: 0;
    bottom: initial;
    padding: 0 1rem;
  }
}
.header__content {
  max-width: 60.5rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 48em) {
  .header__content {
    height: calc(var(--header-height) + 1.5rem);
    column-gap: 1rem;
  }
}
.header__logo {
  color: var(--title-color);
  font-weight: var(--font-medium);
}
.header__logo:hover {
  color: var(--first-color);
}
@media (max-width: 21.875em) {
  .header__nav {
    padding: 2rem 0.25rem 4rem;
  }
}
@media (max-width: 47.9375em) {
  .header__nav {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -0.0625rem 0.25rem rgba(0, 0, 0, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: 0.3s;
    transform: translateY(100%);
    transition: transform 1s;
  }
}
@media (min-width: 48em) {
  .header__nav {
    margin-left: auto;
  }
}
.header__list {
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  padding-left: 0;
}
@media (max-width: 21.875em) {
  .header__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 48em) {
  .header__list {
    display: flex;
    column-gap: 2rem;
  }
}
.header__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
}
.header__link:hover {
  color: var(--first-color);
}
.header__icon {
  font-size: 1.2rem;
}
@media (min-width: 48em) {
  .header__icon {
    display: none;
  }
}
.header__close {
  position: absolute;
  right: 1.3rem;
  bottom: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--first-color);
}
@media (min-width: 48em) {
  .header__close {
    display: none;
  }
}
.header__close:hover {
  color: var(--first-color-alt);
}
.header__btn {
  display: flex;
  align-items: center;
}
.header .change-theme {
  font-size: 1.25rem;
  color: var(--title-color);
  margin-right: var(--mb-1-5);
  cursor: pointer;
}
@media (min-width: 48em) {
  .header .change-theme {
    margin: 0;
  }
}
.header .change-theme:hover {
  color: var(--first-color);
}
.header__toggle {
  color: var(--title-color);
  font-weight: var(--font-medium);
  font-size: 1.1rem;
  cursor: pointer;
}
@media (min-width: 48em) {
  .header__toggle {
    display: none;
  }
}
.header__toggle:hover {
  color: var(--first-color);
}
.header .show_menu {
  transform: translateY(0);
}

.scroll-header {
  box-shadow: 0 -0.0625rem 4px rgba(0, 0, 0, 0.15);
  background-color: var(--body-color);
}

.scrollup {
  position: fixed;
  right: 1rem;
  bottom: -20%;
  background-color: var(--first-color);
  opacity: 0.8;
  padding: 0 0.3rem;
  border-radius: 0.4rem;
  z-index: var(--z-tooltip);
  transition: 0.3s;
}

.scrollup:hover {
  background-color: var(--first-color-alt);
}

.scrollup__icon {
  font-size: 1.5rem;
  color: var(--container-color);
}

.show-scroll {
  bottom: 5rem;
}

::-webkit-scrollbar {
  width: 0.6rem;
  background-color: var(--scroll-bar-color);
  border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb {
  background-color: var(--scroll-thumb-color);
  border-radius: 0.5rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--text-color-light);
}

@media (min-width: 35.5em) {
  .about__container {
    grid-template-columns: 1fr 1fr;
  }
}
.about__img {
  width: 300px;
  border-radius: 0.5rem;
  justify-self: center;
  align-self: center;
}
@media (min-width: 35.5em) {
  .about__img {
    width: 100%;
  }
}
@media (max-width: 60.5em) {
  .about__img {
    height: 90%;
    object-fit: cover;
    width: 100%;
  }
}
.about__description {
  text-align: center;
  margin-bottom: var(--mb-2-5);
}
@media (min-width: 35.5em) {
  .about__description {
    text-align: left;
  }
}
.about__info {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: var(--mb-2-5);
}
@media (min-width: 48em) {
  .about__info {
    justify-content: space-between;
  }
}
.about__info--number {
  font-size: var(--h2-font-size);
  font-weight: var(--font-semi-bold);
  color: var(--title-color);
}
.about__info--number, .about__info--text {
  display: block;
  text-align: center;
}
.about__info--text {
  font-size: var(--smaller-font-size);
}
.about__btn {
  display: flex;
  justify-content: center;
}
@media (min-width: 48em) {
  .about__btn {
    justify-content: initial;
  }
}

.skills {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  align-items: center;
  justify-content: center;
}
.skills img {
  width: 7%;
}
@media (max-width: 47.9375em) {
  .skills img {
    width: 9%;
  }
}

.experience__tabs {
  display: flex;
  margin-bottom: var(--mb-2);
  justify-content: space-evenly;
}
@media (max-width: 21.875em) {
  .experience__tabs {
    justify-content: space-between;
  }
}
.experience__button {
  font-size: var(--h3-font-size);
  font-weight: var(--font-medium);
  cursor: pointer;
}
@media (min-width: 48em) {
  .experience__button {
    margin: 0 var(--mb-1);
  }
}
.experience__button:hover {
  color: var(--first-color);
}
.experience__icon {
  font-size: 1.8rem;
  margin-right: var(--mb-0-25);
}
.experience__sections {
  display: grid;
  gap: 2.5rem;
}
@media (min-width: 35.5em) {
  .experience__sections {
    grid-template-columns: 0.6fr;
    justify-content: center;
  }
}
@media (min-width: 48em) {
  .experience__sections {
    grid-template-columns: 0.5fr;
  }
}
.experience__text {
  display: grid;
  grid-template-columns: 1fr max-content 1fr;
  column-gap: 1.5rem;
}
@media (max-width: 21.875em) {
  .experience__text {
    gap: 0.5rem;
  }
}
.experience__title {
  font-size: var(--normal-font-size);
  font-weight: var(--font-medium);
}
.experience__subtitle {
  display: inline-block;
  font-size: var(--small-font-size);
  margin-bottom: var(--mb-1);
}
.experience__date {
  font-size: var(--smaller-font-size);
  color: var(--text-color-light);
}
.experience__rounder {
  display: inline-block;
  width: 0.8125rem;
  height: 0.8125rem;
  background-color: var(--first-color);
  border-radius: 50%;
}
.experience__line {
  display: block;
  width: 0.0625rem;
  height: 100%;
  background-color: var(--first-color);
  transform: translate(6px, -7px);
}
.experience [data-content] {
  display: none;
}
.experience__active[data-content] {
  display: block;
}

.experience__button.experience__active {
  color: var(--first-color);
}

.projet__container {
  overflow: initial;
  height: 65vh;
  overflow-y: scroll;
}
.projet__body {
  gap: 2rem;
  display: grid;
}
@media (min-width: 35.5em) {
  .projet__body {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 64em) {
  .projet__body {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.projet__content {
  box-shadow: 0 2.4rem 4.8rem rgba(0, 0, 0, 0.08);
  border-radius: 0.5rem;
  overflow: hidden;
}
.projet__img {
  width: 100%;
}
.projet__text {
  padding: 0 1rem 1rem 1rem;
}
.projet__title {
  font-size: var(--h3-font-size);
  margin-bottom: var(--mb-0-5);
}
.projet__paragraph {
  margin-bottom: var(--mb-0-75);
}
.projet__icon {
  font-size: 1.5rem;
  margin-left: var(--mb-0-5);
}
.projet__technologie {
  display: flex;
  flex-wrap: wrap;
  gap: 0.7rem;
}
.projet__technologie--item {
  border: 0.0625rem solid var(--first-color-lighter);
  background-color: var(--first-color-lighter);
  padding: 0.4rem;
  font-size: var(--small-font-size);
  border-radius: 0.1rem;
}
.projet__button {
  padding: 0.5rem 1rem;
}

.contact__container {
  row-gap: 3rem;
}
@media (min-width: 35.5em) {
  .contact__container {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 35.5em) {
  .contact__information {
    gap: 2rem;
    display: flex;
    flex-direction: column;
  }
}
.contact__icon {
  font-size: 2rem;
  color: var(--first-color);
  margin-right: var(--mb-0-75);
}
@media (max-width: 21.875em) {
  .contact__icon {
    font-size: 1.5rem;
  }
}
.contact__name {
  font-size: var(--h3-font-size);
  color: var(--text-color);
}
@media (max-width: 21.875em) {
  .contact__name {
    font-size: var(--normal-font-size);
  }
}
.contact__content {
  background-color: var(--input-color);
  border-radius: 0.5rem;
  padding: 0.75rem 1rem 0.25rem;
}
.contact__label {
  font-size: var(--small-font-size);
  color: var(--text-color);
}
.contact__input {
  width: 100%;
  background-color: var(--input-color);
  color: var(--text-color);
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  border: none;
  outline: none;
  padding: 0.25rem 0.5rem 0.5rem 0;
}
.contact__submit {
  justify-self: start;
}
.contact__input--submit {
  background-color: var(--first-color);
  width: 100%;
  color: var(--container-color);
  border: none;
}

.footer {
  padding-top: 2rem;
}
.footer__bg {
  background-color: var(--first-color);
}
.footer__container {
  row-gap: 3.5rem;
  padding: 2rem 1rem 3rem 1rem;
}
.footer__title {
  font-size: var(--h1-font-size);
  margin-bottom: var(--mb-0-25);
}
.footer__subtitle {
  font-size: var(--small-font-size);
}
.footer__sociallink {
  font-size: 1.25rem;
  margin-right: var(--mb-1-5);
}
.footer__sociallink:hover {
  color: var(--first-color-lighter);
}
.footer__copyright {
  font-size: var(--smaller-font-size);
  text-align: center;
  color: var(--text-color-light);
}
.footer__title, .footer__subtitle, .footer__sociallink {
  color: var(--container-color);
}